@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  color: #333;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  border-bottom: 1px solid #333;
}

.navbar h1 {
  font-size: 30px;
}

.navbar a {
  text-decoration: none;
  transition: opacity 0.3s;
}

.navbar a:hover {
  opacity: 0.7;
}

.item-preview {
  max-width: 300px;
  margin: 20px;
  padding: 10px 15px;
  border: 1px solid #333;
  transition: border 0.3s;
  cursor: pointer;
}

.item-preview:hover {
  border: 1px solid orange;
}

.item-preview h4 {
  font-size: 20px;
  color: #333;
  margin-bottom: 5px;
}

.item-preview a {
  text-decoration: none;
}

.item-details {
  padding: 10px 20px;
}

.choices {
  padding: 10px 0;
}

.dropdown {
  min-width: 150px;
  padding: 10px;
}

